const Skills = ['ReactJS', "React Native", "PHP", "ExpressJS", "FeathersJS", "Redux", "SQL", "MongoDB", "Firebase", "PassportJS"];

const renderTags = (keywords) => {
    return keywords.map((skill) => {
        return (<React.Fragment key={skill}><Chip className='icon' label={skill} style={{margin: 3}}/>{" "}
        </React.Fragment>)
    })
}

const renderProjects = () => {
    return Projects.projects.map((project, index) => {
        return (
            <Grid style={{padding: 25}} key={index}>
                <Grid xs={12} style={{
                    textAlign: 'center',
                    padding: 10,
                    fontFamily: 'cursive',
                    fontSize: 22
                }}>
                    {
                        project.url !== '#' ?
                            <a href={project.url} target="_blank"
                               style={{textDecoration: 'none'}}>#{index + 1} {project.name}</a>
                            :
                            `#${index + 1} ${project.name}`
                    }
                </Grid>
                <Grid xs={12} style={{textAlign: 'center', padding: 10}}><i>{project.shortDescription}</i></Grid>
                <Grid xs={12} style={{textAlign: 'center'}}>{project.description}</Grid>
                <Grid xs={12} style={{textAlign: 'center', paddingTop: 10}}>{renderTags(project.keywords)}</Grid>
            </Grid>
        )
    })
}

const renderAchievment = (itemArr) => {
    return itemArr.achievement.map((item, index) => {
        return (
            <ListItem key={index}>
                <ListItemIcon>
                    <StarBorder/>
                </ListItemIcon>
                <ListItemText primary={item}/>
            </ListItem>
        )
    })
}

